/* 选框与文本对齐   */
.p-report_view   input[type=radio],.p-report_view  input[type=checkbox] {
    position: relative;
    *top: 1px;
    margin: 0 4px 0 0;
    *margin: 0 0 0 -4px;
  }

.p-report_view  .m-form .ipt .u-radio *{
    *vertical-align: baseline;
  }

.p-report_view{
  .u-radio{
    line-height:30px;
  }
  .formitm{
    padding-top: 10px;
  }
  .m-table{
    margin-top: 20px;
  }
}
.p-report_view .m-form{ height: auto; }


